class InterestPeriodComponent extends React.Component {
  constructor(props) {
	super(props);
	this.state = { 
		baseRateState : this.props.baseRate,
		commissionRateState : this.props.commissionRate,
		yearMonthState : this.props.yearMonth
	};
  }
  
  handleChangeBaseRate = (e) => {
        let value = e.target.value; //.replace(/\D/g, "");
		if(parseInt(value) > 0) {
			value = value.replace(/^0+/, '');
		}
		this.setState({baseRateState : value});
  };
  
  handleChangeCommissionRateState = (e) => {
        let value = e.target.value; //.replace(/\D/g, "");
		if(parseInt(value) > 0) {
			value = value.replace(/^0+/, '');
		}
		this.setState({commissionRateState : value});
  };
	
  render() {
	let localYear = undefined;
	let localMonth = undefined;
	if(this.props != undefined && this.state.yearMonthState != undefined) {
		localYear = this.state.yearMonthState.split('_')[0];
		localMonth = this.state.yearMonthState.split('_')[1];
	}
	
    return (<div style={{display: "inline-block", border: "0.1vh solid #d4d5d6", padding: "4vh 2vh 2vh 2vh", marginTop: '15px', marginBottom: '15px', marginLeft: "10px", marginRight: "10px"}}>
			<LocalizationProvider locale={pl} dateAdapter={AdapterDateFns}>
			 <DatePicker
				  views={['year', 'month']}
				  label="Początek obowiazywania"
				  minDate={dayjs(new Date())}
				  value={new Date(parseInt(localYear),parseInt(localMonth)-1,1)}
				  onChange={(newDate)=>{console.log('Executing on change! ' + newDate);
										if(newDate != undefined) {
										let month = newDate.getMonth() + 1;
										month = (month < 10) ? '0' + month.toString() : month.toString();
										let yearMonth = newDate.getFullYear() + '_' + month;
										console.log('On Change Yearmonth is ' + yearMonth);
										this.setState({yearMonthState : yearMonth});
									}
				  }}
				  onAccept={(newDate)=> {
					    console.log('Executing on Accept!');
						if(newDate != undefined) {
							let month = newDate.getMonth() + 1;
							month = (month < 10) ? '0' + month.toString() : month.toString();
							let yearMonth = newDate.getFullYear() + '_' + month;
							console.log('Yearmonth is ' + yearMonth);
							this.setState({yearMonthState : yearMonth});
						    this.props.sendUpdate('interestPeriod', 'UPDATE', {key: 'yearMonth', value : yearMonth, order: this.props.order});
						}
				  }}
				  renderInput={(params) => <TextField {...params} helperText={null} />}
				/>
			</LocalizationProvider>
    <br/><br/>
	      <FormControl sx={{ m: 1 }}>
          <InputLabel htmlFor="outlined-adornment-amount">WIBOR</InputLabel>
          <OutlinedInput type="number"
            id="outlined-adornment-amount"
            value={this.state.baseRateState}
            onChange={this.handleChangeBaseRate}
			onBlur={(event)=> {
						let value = undefined;
						value = event.target.value;
						if(value === "") {
							value = 0;
							this.setState({baseRateState : value});
						}
						this.props.sendUpdate('interestPeriod', 'UPDATE', {key: 'baseRate', value : value, order: this.props.order});
			}}
            startAdornment={<InputAdornment position="start">%</InputAdornment>}
            label="WIBOR"
          />
		</FormControl>
	    <FormControl sx={{ m: 1 }}>
          <InputLabel htmlFor="outlined-adornment-amount">Marża</InputLabel>
          <OutlinedInput type="number"
            id="outlined-adornment-amount"
            value={this.state.commissionRateState}
            onChange={this.handleChangeCommissionRateState}
			onBlur={(event)=> {
						let value = undefined;
						value = event.target.value;
						if(value === "") {
							value = 0;
							this.setState({commissionRateState : value});
						}
						this.props.sendUpdate('interestPeriod', 'UPDATE', {key: 'commissionRate', value : value, order: this.props.order});
			}}
            startAdornment={<InputAdornment position="start">%</InputAdornment>}
            label="Marża"
          />
        </FormControl>
		<br/>
		<Button spacing={3}  variant="outlined" 
		onClick={()=>{this.props.sendUpdate('interestPeriod', 'DELETE', {value : this.props.order});}}
		>Usuń</Button>
		</div>);
  }
}

